import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-video-player',
  templateUrl: './video-player.component.html',
  styleUrls: ['./video-player.component.scss']
})

export class VideoPlayerComponent implements OnInit {
  public play: Subject<void> = new Subject<void>();
  public pause: Subject<void> = new Subject<void>();
  public setTime: Subject<void> = new Subject<void>();
  public updateCue: Subject<any> = new Subject<any>();
  public resetPlayer: Subject<any> = new Subject<any>();
  public showCorrectAns: Subject<any> = new Subject<any>();
  public resetQuestion: Subject<any> = new Subject<any>();
  public removeDisable: Subject<any> = new Subject<any>();
  notePath;
  currCueAction;
  isNote = false;
  videoPlayerdata;
  constructor( private http: HttpClient) {
     this.getVideoData().subscribe(res => {
      console.log(res);
      this.videoPlayerdata = res;
    });;
  }

  ngOnInit(): void {}

  onVideoLoaded() {
    this.play.next();
  }
  cuePointTriggerControler(e) {
    this.currCueAction = e;
    const data = {
      resourceId: e.action.params.subContentId
    };
    if (e.action.library === 'note') {
      this.loadNote();
    } else {
      // this.cuePointTrigger(data);
    }
  }

  loadNote() {
    this.notePath =
      'https://white-warrior.s3.ap-south-1.amazonaws.com/note-lib/216/note.json';
    this.isNote = true;
  }

  getStyle() {
    const style = {
      position: 'absolute',
      overflow: 'auto',
      height: this.currCueAction.action.height + '%',
      width: this.currCueAction.action.width + '%',
      top: this.currCueAction.action.y + '%',
      left: this.currCueAction.action.x + '%'
    };
    return style;
  }

  onEndCuePointTrigger() {
    this.isNote = false;
  }
  getVideoData() {
    return this.http.get("assets/video.json")
}
}
